@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap);
* {
    margin: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
}

body {
    height: 100vh;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(89, 149, 175);
    background-color: #676b9f;
}
.add-user-form {
    padding: 2rem;
    background-color: #fff;
    border-radius: 0.5rem;
    box-shadow: 0 1rem 2rem 0 #0000001f;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    min-width: 45rem;
    font-size: 1rem;
}

#username,
#age {
    border: none;
    outline: none;
    background-color: #00000010;
    padding: 0.5rem;
    border-radius: 0.5rem;
    min-width: 15rem;
    font-size: 1rem;
}

#username:focus,
#age:focus {
    background-color: #00000017;
}

#submitBtn {
    color: #fff;
    font-weight: 600;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 0.5rem;
    outline: none;
    background-color: cadetblue;
    cursor: pointer;
    font-size: 1rem;
}

#submitBtn:hover {
    background-color: rgb(78, 134, 136);
}
.error-bg {
    position: fixed;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    top: 0;
    left: 0;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.error-card {
    background-color: #fff;
    padding: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
    color: #000;
    border-radius: 1rem;
    min-width: 25rem;
}

.exitBtn {
    text-align: center;
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: coral;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    width: 8rem;
}

.exitBtn:hover {
    background-color: rgb(255, 108, 55);
}
.users-list-div {
    padding: 2rem;
    background-color: #fff;
    border-radius: 0.5rem;
    box-shadow: 0 1rem 2rem 0 #0000001f;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-width: 45rem;
    font-size: 1rem;
}

.users-list {
    padding: 1rem;
    list-style: none;
}

.list-items {
    margin: 0.5rem 0;
    padding: 0.5rem 1rem;
    border: 1px solid grey;
    border-radius: 0.25rem;
}
.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-row-gap: 5rem;
    row-gap: 5rem;
}
